@import url(https://fonts.googleapis.com/css2?family=Nunito);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #282c34;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{margin-top:25px;width:100%;padding-left:16px;padding-right:16px}header a{display:flex;flex-direction:row;justify-items:center;align-items:center;letter-spacing:1.5px}header a .photo{height:50px;width:50px;border-radius:50px;object-fit:cover;margin-right:8px}@media only screen and (min-width: 768px){header{width:700px}}

.hero{width:100%;padding-top:50px;padding-right:16px;padding-left:16px}.hero .intro{margin-top:-10px;background-color:rgba(189,189,189,0.1);color:white;font-family:'Nunito';margin-bottom:25px;padding:8px;border-radius:16px;font-size:8px}.hero .intro .name{font-size:16px}.hero .intro .wrap-icon{margin-top:10px;display:flex;flex-direction:row;justify-content:space-between}.hero .intro .wrap-icon a{margin-right:16px}@media only screen and (min-width: 768px){.hero{width:700px}.hero .intro{font-family:'Nunito';padding-right:16px;padding-left:16px;font-size:18px}.hero .intro .name{font-size:32px}.hero .intro .wrap-icon{justify-content:flex-start}.hero .row{display:flex;flex-direction:row;justify-content:center;align-items:center}}

.card{width:100%;font-family:'Nunito', sans-serif;color:white;font-size:16px;background-color:rgba(189,189,189,0.1);padding:16px;border-radius:10px;margin-bottom:16px;margin-right:8px}.card .title{font-weight:bold;letter-spacing:2px}.card .stack{font-weight:lighter;margin-bottom:0px;font-size:14px}.card .description{margin-bottom:0;letter-spacing:1px;font-weight:lighter;font-size:14px}.card .link{color:gray}@media only screen and (min-width: 768px){.card{width:700px}}

footer{padding-top:25px;padding-bottom:25px}

.home{max-width:100vw;display:flex;flex-direction:column;justify-content:center;align-items:center}.home a{text-decoration:none;color:white;font-size:12px;font-family:'Nunito';text-transform:lowercase}

