.hero {
  width: 100%;
  padding-top: 50px;
  padding-right: 16px;
  padding-left: 16px;
  .intro {
    margin-top: -10px;
    background-color: rgba(189, 189, 189, 0.1);
    color: white;
    font-family: 'Nunito';
    margin-bottom: 25px;
    padding: 8px;
    border-radius: 16px;
    font-size: 8px;
    .name {
      font-size: 16px;
    }
    .wrap-icon {
      margin-top: 10px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      a {
        margin-right: 16px;
      }
    }
  }
  // .row {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: center;
  //   align-items: center;
  //   flex-wrap: wrap;
  //   flex-grow: 1;
  // }
}

@media only screen and (min-width: 768px) {
  .hero {
    width: 700px;
    .intro {
      font-family: 'Nunito';
      padding-right: 16px;
      padding-left: 16px;
      .name {
        font-size: 32px;
      }
      font-size: 18px;
      .wrap-icon {
        justify-content: flex-start;
      }
    }
    .row {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
  }
}
