.card {
  // min-height: 120px;
  width: 100%;
  font-family: 'Nunito', sans-serif;
  color: white;
  font-size: 16px;
  background-color: rgba(189, 189, 189, 0.1);
  padding: 16px;
  border-radius: 10px;
  margin-bottom: 16px;
  margin-right: 8px;
  .title {
    font-weight: bold;
    letter-spacing: 2px;
  }
  .stack {
    font-weight: lighter;
    margin-bottom: 0px;
    font-size: 14px;
  }
  .description {
    margin-bottom: 0;
    letter-spacing: 1px;
    font-weight: lighter;
    font-size: 14px;
  }
  .link {
    color: gray;
  }
}

@media only screen and (min-width: 768px) {
  .card {
    width: 700px;
  }
}
