header {
  margin-top: 25px;
  width: 100%;
  padding-left: 16px;
  padding-right: 16px;
  a {
    display: flex;
    flex-direction: row;
    justify-items: center;
    align-items: center;
    letter-spacing: 1.5px;
    .photo {
      height: 50px;
      width: 50px;
      border-radius: 50px;
      object-fit: cover;
      margin-right: 8px;
    }
  }
}

@media only screen and (min-width: 768px) {
  header {
    width: 700px;
  }
}
